body {
  background-color: #cbcbf8;
}

.search-form {
  background-color: #5c5cbd;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.input {
  width: 250px;
  height: 30px;
  border: none;
  border-radius: 2%;
  outline: none;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

button {
  height: 30px;
  cursor: pointer;
  border: none;
  border-radius: 2%;
}

.icon {
  width: 15px;
  height: 15px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 73px -15px 20px;
  display: flex;
}

.photo-card {
  cursor: pointer;
  background-color: #ece3e3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
  transform: scale(1);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.photo-card a:hover, .photo-card a:focus {
  transform: scale(1.1);
}

.info {
  width: 220px;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 8px;
  display: flex;
}

.info-item {
  color: #0f0f0f;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  display: flex;
}

.info-text {
  font-weight: bold;
}

.load-more {
  align-items: center;
  margin: 0 auto 15px;
  padding: 20px;
  font-size: large;
  display: flex;
}

.load-more :hover, .load-more :focus {
  background-color: #5c5cbd;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

